___CSS_LOADER_EXPORT___.push([module.id, `.report-container {
    max-width: 600px;
    margin: 0 auto;
    background-color: #333;
    color: white;
    padding: 20px;
    border-radius: 10px;
  }
  
  h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .get-location-btn,
  .submit-btn {
    display: block;
    width: calc(100% - 40px); /* 부모 요소의 padding을 고려한 너비 설정 */
    padding: 10px;
    margin: 10px 0;
    background-color: #0066cc;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    text-align: center;
  }
  
  .get-location-btn:hover,
  .submit-btn:hover {
    background-color: #004c99;
  }
  
  .section {
    margin-bottom: 20px;
  }
  
  input[type="file"] {
    width: calc(100% - 40px); /* 부모 요소의 padding을 고려한 너비 설정 */
    margin-top: 10px;
    padding: 10px;
    background-color: #0066cc;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
  }
  
  input[type="text"],
  select {
    width: calc(100% - 40px); /* 부모 요소의 padding을 고려한 너비 설정 */
    padding: 10px;
    margin-top: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #444;
    color: white;
